import Link from 'next/link';
import { useEffect, useState } from 'react';

export default function Navbar() {
const [user, setUser] = useState(null);

useEffect(() => {
const t = localStorage.getItem('token');
setUser(!!t);
}, []);

const logout = () => {
localStorage.removeItem('token');
window.location.href = '/';
};

return ( <nav className="bg-white shadow-md"> <div className="container flex items-center justify-between py-4 px-6">
<Link href="/" className="text-2xl font-bold" style={{ color: 'var(--brand)' }}>
Trust <span style={{ color: 'var(--accent)' }}>Market</span> </Link> <div className="flex items-center space-x-4"> <Link href="/" className="text-gray-600">Home</Link> <Link href="/dashboard" className="text-gray-600">Dashboard</Link>
{user ? ( <button onClick={logout} className="btn btn-ghost">Logout</button>
) : (
<> <Link href="/login" className="btn btn-primary">Login</Link> <Link href="/register" className="ml-2 btn btn-ghost">Register</Link>
</>
)} </div> </div> </nav>
);
}
